.TodoEditor{
    width: 600px;
    display: flex;
    flex-direction: column;
    padding: 16px;
    border: 1px solid black;
    border-radius: 4px;
    margin-bottom: 20px;
    margin-left: 40px;
}

.TodoEditor__textarea{
    line-height: 16px;
    height: 16px*3*24px;
    padding: 12px 16px;
    border: 1px solid rgba(33,33,33,0.2);
    border-radius: 4px;

    font: inherit;
    line-height: 16px;
    letter-spacing: 0.01em;

    resize: none;
    margin-bottom: 12px;
}

.TodoEditor__btn{
    background-color: cadetblue;
    min-height: 50px;
    width: 100%;
}